  font-weight: 600;
  display: flex;
}

.nav__heading {
  font-size: 1.4rem;
}

.nav__mode {
  cursor: pointer;
  font-size: 1.4rem;
  display: flex;
}

@media screen and (min-width: 481px) and (max-width: 768px) {
  .nav__heading, .nav__mode {
    font-size: 1.8rem;
