.select {
  background-color: var(--elements);
  color: var(--text);
  cursor: pointer;
  border: none;
  border-radius: 1rem;
  margin-top: 4rem;
  margin-left: 2rem;
  padding: 1.5rem 3rem;
  box-shadow: 1px 1px 10px #0003;
}

.select:focus {
  background-color: var(--elements);
  outline: none;
}

@media screen and (min-width: 769px) and (max-width: 1024px) {
  .select {
    font-size: 1.6rem;
  }
}

@media screen and (min-width: 1025px) {
  .select {
    font-size: 1.8rem;
  }
}

@keyframes slidein {
  from {
    width: 100%;
    margin-top: 100%;
  }

  to {
    width: 100%;
    margin-top: 0%;
  }
}

.countries {
  animation-name: slidein;
  animation-duration: 1s;
  display: grid;
}

.countries__country {
  cursor: pointer;
  width: 25rem;
  height: 17.5rem;
  border-radius: 1rem 1rem 0 0;
  flex-direction: column;
  margin: 5rem auto 25rem;
  transition: transform 1s;
  display: flex;
}

.countries__country:hover {
  transform: translateY(-10px);
}

.countries__country-flag {
  background-color: var(--elements);
}

.countries__country-flag--img {
  width: 25rem;
  height: 17.5rem;
  border-radius: 1rem 1rem 0 0;
}

.countries__details * {
  margin-bottom: .5rem;
  margin-left: 2rem;
}

.countries__details {
  background-color: var(--elements);
  width: 25rem;
  color: var(--text);
  border-radius: 0 0 1rem 1rem;
  margin: 0 auto;
  padding-bottom: 2rem;
  font-weight: 600;
}

.countries__details--name {
  margin-top: 2rem;
  margin-bottom: 2rem;
  font-size: 1.9rem;
}

@media screen and (min-width: 481px) and (max-width: 768px) {
  .countries {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 769px) and (max-width: 1024px) {
  .countries {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (min-width: 1025px) {
  .countries {
    grid-template-columns: repeat(4, 1fr);
  }
